<template>
	<view class="all">
		<view class="head">
			<view class="contentTitle">{{contents[0].input}}</view>
			<view class="date">
				{{contents[0].date}}
			</view>
		</view>
		<view>
			心事描述
		</view>
		<view class="content">
			<view>
				<text space="emsp">{{contents[0].textarea}}</text>
			</view>
			<view class="images">
				<image @click="previewImage(item)" v-for="(item,index) in contents[0].image" :key="index" :src="item"
					mode="aspectFill"></image>
			</view>
		</view>
		<uni-fab icon="more" @fabClick="fabClick" :pattern="pattern" vertical="bottom" horizontal="right"
			:popMenu="false"></uni-fab>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				contents: [],
				id: 0,
				pattern: {
					buttonColor: "#6365b7"
				}
			}
		},
		onLoad(option) {
			this.id = option.id
			this.getContent(option.id)
		},
		onShow() {
			this.getContent(this.id)
		},
		onPullDownRefresh() {
			setTimeout(() => {
				uni.stopPullDownRefresh()
			}, 1000)
		},
		methods: {
			getContent(id) {
				const db = uniCloud.database()
				db.collection('HeardEvent').where({
					_id: id
				}).get().then((res) => {
					this.contents = res.result.data
					console.log(this.contents)
				})
			},
			previewImage(url) {
				uni.previewImage({
					current: url,
					urls: this.contents[0].image,
					indicator: 'number'
				})
			},
			fabClick() {
				uni.showActionSheet({
					itemList: ["编辑", "删除"],
					itemColor: "red",
					success: (res) => {
						const db = uniCloud.database()
						if (res.tapIndex == 1) {
							db.collection("HeardEvent").where({
								_id: this.id
							}).remove().then(() => {
								console.log("删除成功")
								uni.switchTab({
									url: '../HeardEvent/HeardEvent'
								})
							})
						} else {
							uni.navigateTo({
								url: `/pages/updateEvent/updateEvent?id=${this.id}`
							})
						}
					},
					fail: function(res) {
						console.log(res.errMsg);
					}
				});

			}
		}
	}
</script>

<style lang="scss">
	.all {
		margin: 20rpx;

		.head {
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			align-items: center;
			background: white;
			border-radius: 30rpx;
			height: 250rpx;
			box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

			.contentTitle {
				color: #6365b7;
			}

			.day {
				color: #6365b7;
				font-size: 50rpx;
			}

			.date {
				color: #a6a6a6;
			}
		}

		view:nth-child(2) {
			margin: 20rpx;
			color: #a6a6a6
		}

		.content {
			box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
			background: white;
			border-radius: 30rpx;

			view:nth-child(1) {
				padding: 30rpx;
			}

			.images {
				display: flex;
				flex-wrap: wrap;

				image {
					margin: 10rpx;
				}
			}
		}
	}
</style>